@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Regular.woff2") format("woff2"),url("../../fonts/Inter-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Medium.woff2") format("woff2"),url("../../fonts/Inter-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: "Gilroy";
  font-display: swap;
  src: url("../../fonts/Gilroy-Bold.woff2") format("woff2"),url("../../fonts/Inter-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
dd,
dt {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Gilroy",sans-serif;
  font-size: 16px;
  color: #000;
  height: 100%;
  min-height: 100vh;
  background: #fff;
  width: 100%;
}

main {
  flex: 1;
}

.lock {
  overflow: hidden;
}

.uppercase {
  text-transform: uppercase;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
  transition: .3s ease-in-out;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

button {
  padding: 0;
  border-style: solid;
  border: none;
}

svg {
  display: block;
  transition: .3s ease-in-out;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

input,
textarea {
  outline: none;
}

textarea {
  resize: none;
}

p {
  margin: 0;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.filter {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 50px;
}

.filter h2 {
  font-family: "Gilroy",sans-serif;
  font-size: 24px;
  line-height: 120%;
  color: #000;
}

.filter input {
  padding: 13px 16px;
  font-family: "Gilroy",sans-serif;
  font-size: 14px;
  line-height: 120%;
  color: #565656;
  border: 1px solid #d5d5d5;
  margin-top: 24px;
}

.filter button {
  background: #098fcf;
  font-weight: 600;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  color: #fff;
  padding: 13px 20px;
  cursor: pointer;
  transition: all ease-in-out .3s;
}

.test {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  margin-top: 30px;
}

.test h1 {
  font-family: "Gilroy",sans-serif;
  font-size: 30px;
  line-height: 120%;
  color: #000;
}

.posts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  justify-items: center;
  margin-top: 24px;
  padding-bottom: 30px;
}

.post {
  width: 100%;
  padding: 20px 25px;
  box-shadow: 0px 5px 10px 2px rgba(119,122,124,.2);
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  transition: all ease-in-out .3s;
}

.post__title {
  font-family: "Gilroy",sans-serif;
  font-size: 24px;
  line-height: 120%;
  color: #000;
  flex: 0 0 auto;
  height: 32px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  transition: all ease-in-out .3s;
}

.post__text {
  margin-top: 20px;
  flex: 1 0 auto;
  font-family: "Gilroy",sans-serif;
  font-size: 18px;
  line-height: 120%;
  color: #000;
  transition: all ease-in-out .3s;
}

.post__select {
  margin-top: 20px;
  display: flex;
  align-items: center;
  column-gap: 4px;
  width: fit-content;
  flex: 0 0 auto;
  font-family: "Gilroy",sans-serif;
  font-size: 18px;
  line-height: 120%;
  color: #000;
  cursor: pointer;
  transition: all ease-in-out .3s;
}

.post__select input {
  width: 16px;
  height: 16px;
  margin: 0;
  cursor: pointer;
}

.post--black {
  background-color: #5e3c3c;
}

.post--black .post__title,
.post--black .post__text,
.post--black .post__select {
  color: #fff;
}

@media screen and (min-width: 768px) {
  .lock {
    padding-right: 17px;
  }
}

@media screen and (max-width: 425px) {
  .filter input {
    padding: 8px 6px;
    font-size: 12px;
  }

  .filter button {
    padding: 9px 6px;
    font-size: 12px;
  }
}

@media screen and (max-width: 375px) {
  .posts {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media (hover: hover)and (pointer: fine) {
  .filter button:hover {
    background: #195977;
  }
}